*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-family: sans-serif;
}
body{
  background: #eee;
  background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
}

header{
  background-color: greenyellow;
  width: 100%;
  box-shadow: 0 5px 3px #ccc;
}
header h1{
  color: #fff;
  font-family: 'McLaren',cursive;
  font-weight: 200;
  padding: 10px;
}
.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space:pre-wrap;
  word-wrap: break-word;
}

.note button{
  position: relative;
  float: right;
  margin-right: 10px;
  color: greenyellow;
  border: none;
  width: 36px;
  height: 36px;
  cursor: pointer;
  outline: none;
}
form.create-note{
  position: relative;
  width: 480px;
  margin: 30px auto 20px auto;
  background: #fff;
  padding: 15px;
  border-radius: 7px;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
}

form.create-note input,
form.create-note textarea{
  width: 100%;
  border: none;
  padding: 4px;
  outline: none;
  font-size: 1.2em;
  font-family: inherit;
  resize: none;

}
form.create-note button{
  position: absolute;
  right: 18px;
  bottom: -18px;
  background-color: greenyellow;
  color: #fff;
  border: none;
  border-radius: 50px;
  width: 36px;
  height: 36px;
  box-shadow: 0 1px 3px black;
  cursor: pointer;
  outline: none;
}

html{
  width: 100%;
  min-height: 100vh;
  position: relative;
}

footer{
 position: absolute;
text-align: center;
bottom: 1%;
width: 90%;
height: 2.5rem;
padding-top: 20px;
}
footer p{
  color: #ccc;
}

@media  screen and (max-width: 560px) {
  form.create-note{
    position: relative;
    width: 380px;
    margin: 30px auto 20px auto;
    background: #fff;
    padding: 15px;
    border-radius: 7px;
    box-shadow: 0 1px 5px rgb(138, 137, 137);
  }
}
@media  screen and (max-width: 376px) {
  form.create-note{
    position: relative;
    width: 280px;
    margin: 30px auto 20px auto;
    background: #fff;
    padding: 15px;
    border-radius: 7px;
    box-shadow: 0 1px 5px rgb(138, 137, 137);
  }
}
@media  screen and (max-width: 290px) {
  form.create-note{
    position: relative;
    width: 265px;
    margin: 30px auto 20px auto;
    background: #fff;
    padding: 15px;
    border-radius: 7px;
    box-shadow: 0 1px 5px rgb(138, 137, 137);
  }
}